@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
*{
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
    background-color: #111;
    font-family: 'Roboto', sans-serif;
}

.MuiSvgIcon-root{
    font-size: 1rem !important;
}
.homeScreen{
    padding-bottom: 1.5rem;
}
.nav{
    position: fixed;
    top: 0px;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;

    /* animation */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav_black{
    background-color: #111;
    
}

.nav_contents{
    display: flex;
    justify-content: space-between;
}

.nav_logo{
    position: fixed;
    left: 0;
    top: 10px;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}

.nav_avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}
.banner{
    position: relative;
    color: white;
    object-fit: contain;
    height: 448px;
}

.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title{
    font-size: 3rem;
    font-weight: 700;
    padding-bottom: 0.3rem;
}

.banner_description{
    /* width: 45rem; */
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.9rem;
    max-width: 360px;
    height: 80px;

}

.banner_fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg, transparent, rgba(37,37,37,0.61),#111
    );
}

.banner_button{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    margin-right: 1rem;
    border-radius: 0.2vw;
    background-color: rgba(51,51,51,0.5);
    padding: 0.5rem 2rem;
    font-size: 1rem;

}

.banner_button:hover{
    color: black;
    background-color: #e6e6e6;
    transition: all 0.3s;
}





@media(max-width:500px){
    .banner_title{
        font-size: 2.5rem;
    }
}
.row{
    color:white;
    margin-left:20px;

}

.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_posters::-webkit-scrollbar{
    height: 5px;
    background: #222;
    border-radius: 10px;
    margin: 5px auto;
}

.row_posters::-webkit-scrollbar{
    cursor: pointer;
}

.row_posters::-webkit-scrollbar-thumb:horizontal{
    background: rgb(233, 1, 1);
    border-radius: 10px;
}

.row_poster{
    cursor: pointer;
    max-height: 100px;
    object-fit: contain;
    margin-right:10px ;
    width: 100%;
    transition: -webkit-transform 450ms;
    transition: transform 450ms;
    transition: transform 450ms, -webkit-transform 450ms;
}

.row_poster:hover{
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
    opacity: 1;
}

.row_posterLarge{
    max-height: 250px;
}

.row_posterLarge:hover{
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
    opacity: 1;
}



@media(max-width:500px){
    .row_posters::-webkit-scrollbar{
    display: none;
    }
}
.loginScreen{
    position: relative;
    overflow: hidden;
    height: 100%;
    background: url("https://cdn.hipwallpaper.com/i/98/21/dUyCkp.jpg") center no-repeat;
    background-size: cover;
}

.loginScreen_body{
    z-index: 1;
    width: 100%;
    color: white;
    padding: 20px;
    position: absolute;
    top: 35%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.loginScreen_body h1{
    font-size: 2.5rem;
    margin-bottom: 20px;
}
.loginScreen_body h2{
    font-size: 1.65rem;
    font-weight: 400;
    margin-bottom: 15px;
}

.loginScreen_h3{
    font-size: 1.3rem;
    font-weight: 200;
}



.loginScreen_gradient{
    width: 100%;
    opacity: 1;
    height: 100vh;
    background: rgba(0,0,0,0.4);
    background-image: linear-gradient(
        to top,rgba(0,0,0,0.8) 0,
        rgba(0,0,0,0)60%,
        rgba(0,0,0,0.8)100%
    );
}

.loginScreen_logo{
    position: fixed;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
}

.loginScreen_button{
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: white;
    background-color: #e50914;
    cursor: pointer;
    border: none;
    font-weight: 600;
}

.loginScreen_input{
    margin: 20px;
}


.loginScreen_input >form >input{
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;
}

.loginScreen_getStarted{
    padding: 16px 20px;
    font-size: 0.95rem;
    color: white;
    background-color: #e50914;
    border: none;
    text-transform: uppercase;
    font-weight: 500;
    cursor: pointer;    
}


@media(max-width:500px){
    .loginScreen_body{
        padding: 0px;
        top: 25%;
    }
    .loginScreen_body>h1{
        font-size: 1.8rem;
        margin-bottom: 12px;
    }

    .loginScreen_body>h2{
        font-size: 1.3rem;
        margin-bottom: 10px;
    }
    .loginScreen_body>h3{
        font-size: 1rem;
    }

    .loginScreen_input{
        margin: 20px auto;
    }

    .loginScreen_input>form>input{
        width: 75%;
    }
    .loginScreen_getStarted{
        display: block;
        margin: 7px auto;
    }
}
.signUpScreen{
    max-width: 300px;
    padding:50px 70px;
    margin-left:auto;
    margin-right: auto;
    background: rgba(0,0,0,0.85);
}

.signUpScreen >form >h1{
text-align: left;
margin-bottom: 25px;
}

.signUpScreen >form{
    display: grid;
    flex-direction: column;
}

.signUpScreen >form>input{
    outline-width:0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px;
}

.signUpScreen>form>button{
    padding: 16px 20px;
    font-size: 1rem;
    color: white;
    border-radius: 5px;
    background-color: #e50914;
    border: none;
    cursor: pointer;
    margin-top: 20px;
}

.signUpScreen >form>h4{
    text-align: left;
    margin-top: 15px;
}

.grey{
    color: gray;
}

.link:hover{
    text-decoration: underline;
    cursor: pointer;
    
}

@media(max-width:500px){
    .signUpScreen >form >h1{
        font-size: 1.8rem;
        margin-bottom: 10px;
        }
    .signUpScreen{
        padding: 50px 30px;
        max-width: 250px;
    }
}
.profileScreen{
    height: 100vh;
 
    color: white;
}

.profileScreen_body{
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    width:50%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 8%;
    max-width: 800px;
    min-width: 290px;
}

.profileScreen_info{
    display: flex;
}

.profileScreen_body>h1{
    font-size: 60px;
    font-weight: 400;
    border-bottom: 1px solid #282c24;
    margin-bottom: 20px;
}

.profileScreen_info >img{
height: 100px;
}

.profileScreen_details{
    margin-left: 25px;
    flex: 1 1;
}

.profileScreen_details>h2{
    background-color: gray;
    padding: 15px;
    font-size: 15px;
    padding-left: 20px;
}

.profileScreen_signOut{
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 5%;
    width: 100%;
    background-color: #e50914;
    color: white;
    border: none;
    cursor: pointer;
}

.profileScreen_plans{
    margin-top: 20px;
}

.profileScreen_plans>h3{
    border-bottom: 1px solid #282c24;
    padding-bottom: 10px;

}

@media(max-width:500px)
{
   .profileScreen_info{
       display: inline;
       display: initial;
   }

    .profileScreen_body>h1{
        margin-top: 13px;
        font-size: 35px;
    }

    .profileScreen_details{
        margin: 0;
    }

    .profileScreen_info>img{
        height: 50px;
    }
   .profileScreen_body{
       padding-top: 15%;
   }
}
.planScreen_plans{
    display: flex;
    padding: 20px;
    justify-content: space-between;
    opacity: 0.8;
}

.planScreen_plans:hover{
    opacity: 1;
}



.planScreen_plans >button{
    padding: 10px 20px; font-size: 1rem; color: white; background-color: #e50914; border: none;
}

.planScreen_plans >button:hover{
    cursor: pointer;
}

.planScreen_info >h6{
    color: whitesmoke;
}
