.banner{
    position: relative;
    color: white;
    object-fit: contain;
    height: 448px;
}

.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title{
    font-size: 3rem;
    font-weight: 700;
    padding-bottom: 0.3rem;
}

.banner_description{
    /* width: 45rem; */
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.9rem;
    max-width: 360px;
    height: 80px;

}

.banner_fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg, transparent, rgba(37,37,37,0.61),#111
    );
}

.banner_button{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    margin-right: 1rem;
    border-radius: 0.2vw;
    background-color: rgba(51,51,51,0.5);
    padding: 0.5rem 2rem;
    font-size: 1rem;

}

.banner_button:hover{
    color: black;
    background-color: #e6e6e6;
    transition: all 0.3s;
}





@media(max-width:500px){
    .banner_title{
        font-size: 2.5rem;
    }
}